  background: #029be5;
}
.yj-manage .change-i i {
  left: 32px;
}
.yj-manage .change-i span {
  margin-left: -15px;
}
.yj-manage .tables tr {
  height: 50px;
}
.yj-manage .tables tr .fa-plus-square-o {
  display: none;
}
.yj-manage .tables tr:last-child .fa-plus-square-o {
  display: inline-block;
}
.yj-manage .tables tr .title {
  cursor: pointer;
}
.yj-manage .tables label {
  position: relative;
  margin: 0 5px;
}
.yj-manage .tables label .yj-tips {
  display: none;
  position: absolute;
  left: 50%;
  bottom: -40px;
  padding: 5px;
  width: 210px;
  color: #fff;
  text-align: center;
  background: rgba(0, 109, 175, 0.8);
  margin-left: -105px;
  border-radius: 3px;
  z-index: 1;
}
.yj-manage .tables label .yj-tips::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 100%;
  width: 0;
  height: 0;
  border: 3px solid transparent;
  border-bottom-color: rgba(0, 109, 175, 0.8);
  margin-left: -3px;
}
.yj-manage .tables label:hover .yj-tips {
  display: block;
}
